<template>
	<view class="scenic-detail-container">
		
		<!-- 图片轮播区 -->
		<swiper class="image-swiper" indicator-dots autoplay circular>
			<swiper-item v-for="(img, index) in scenicData.images" :key="index">
				<image class="swiper-image" :src="img" mode="aspectFill" />
			</swiper-item>
		</swiper>

		<!-- 景点基本信息 -->
		<view class="basic-info">
			<view class="title-section">
				<text class="scenic-name">{{ scenicData.name }}</text>
			</view>

			<view class="info-row">
				<uni-icons type="location-filled" size="18" color="#666"></uni-icons>
				<text class="info-text">{{ scenicData.address }}</text>
			</view>

			<view class="info-row">
				<uni-icons type="clock" size="18" color="#666"></uni-icons>
				<text class="info-text">{{ scenicData.openTime }}</text>
			</view>

			<view class="info-row">
				<uni-icons type="money" size="18" color="#666"></uni-icons>
				<text class="info-text">{{ scenicData.ticketPrice }}</text>
			</view>
		</view>

		<!-- 景点介绍 -->
		<view class="section">
			<view class="section-title">
				<text>景点介绍</text>
			</view>
			<view class="section-content">
				<text class="description">{{ scenicData.description }}</text>
			</view>
		</view>

		<!-- 用户评价 -->
		<view class="section">
			<view class="section-title">
				<text>用户评价</text>
			</view>

			<!-- 评价列表 -->
			<view class="review-list">
				<view class="review-item" v-for="(review, index) in scenicData.reviews" :key="index">
					<image class="user-avatar" :src="review.avatar" mode="aspectFill" />
					<view class="review-content">
						<view class="user-info">
							<text class="username">{{ review.username }}</text>
						</view>
						<text class="review-text">{{ review.content }}</text>
						<text class="review-time">{{ formatTime(review.time) }}</text>

						<!-- 评论图片 -->
						<view class="review-images" v-if="review.images.length > 0">
							<image 
								v-for="(img, imgIndex) in review.images" 
								:key="imgIndex"
								class="review-image"
								:src="img"
								mode="aspectFill"
								@click="previewImage(review.images, imgIndex)"
							/>
						</view>
					</view>
				</view>
			</view>
		</view>

		<!-- 底部操作栏 -->
		<view class="action-bar">
			<button class="action-btn collect-btn" @click="toggleCollect">
				<uni-icons 
					:type="isCollected ? 'heart-filled' : 'heart'" 
					size="20" 
					:color="isCollected ? '#ff5a5f' : '#666'"
				></uni-icons>
				<text>{{ isCollected ? '已收藏' : '收藏' }}</text>
			</button>
			<button class="action-btn review-btn" @click="showReviewModal">
				<uni-icons type="compose" size="20" color="#fff"></uni-icons>
				<text>写评价</text>
			</button>
		</view>

		<!-- 评价弹窗 -->
		<uni-popup ref="reviewPopup" type="bottom">
			<view class="review-modal">
				<view class="modal-header">
					<text class="modal-title">发表评价</text>
					<uni-icons type="closeempty" size="20" @click="closeReviewModal"></uni-icons>
				</view>
				<textarea 
					v-model="newReview.content" 
					class="review-textarea" 
					placeholder="写下你的真实体验..."
					maxlength="200"
					auto-height
				/>
				<view class="image-uploader">
					<uni-file-picker 
						v-model="newReview.images"
						fileMediatype="image"
						mode="grid"
						limit="9"
					/>
				</view>
				<button class="submit-btn" @click="submitReview">提交评价</button>
			</view>
		</uni-popup>
	</view>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { onLoad } from '@dcloudio/uni-app';
import { http, ENV_CONFIG } from '@/api/http';

const scenicId = ref(null);
const scenicData = ref({
  images: [],
  reviews: []
});
const isCollected = ref(false);
const newReview = ref({
  content: '',
  images: []
});

// 返回上一页
const goBack = () => {
  uni.navigateBack();
};

// 格式化时间显示
const formatTime = (timeString) => {
  if (!timeString) return '';
  const date = new Date(timeString);
  return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')}`;
};

// 图片预览
const previewImage = (urls, index) => {
  uni.previewImage({
    current: index,
    urls: urls
  });
};

// 加载景点详情
const fetchDetail = async () => {
  try {
    const response = await http.get(`/api/scenic-spots/${scenicId.value}`);
    const data = response.data.data;

    // 处理图片路径
    const parseImages = (imgString) => {
        try {
            return JSON.parse(imgString).map(img => 
                img ? `${ENV_CONFIG[process.env.NODE_ENV]}${img}` : '/static/default-scenic.jpg'
            );
        } catch {
            return ['/static/default-scenic.jpg'];
        }
    };

    // 处理评论数据
    const parseReviews = (reviews) => {
        return reviews?.map(review => ({
            ...review,
            // 移除冗余的JSON解析，因为后端已处理
            images: review.images,
            time: review.time || review.created_at
        })) || [];
    };

    scenicData.value = {
      ...data,
      images: parseImages(data.images),
      address: data.address,
      openTime: data.open_time,
      ticketPrice: data.ticket_price,
      reviews: parseReviews(data.reviews)
    };

  } catch (error) {
    uni.showToast({ title: '数据加载失败', icon: 'none' });
  }
};

// 提交评价
const submitReview = async () => {
  if (!newReview.value.content.trim()) {
    return uni.showToast({ title: '评论内容不能为空', icon: 'none' });
  }

  try {
    const token = uni.getStorageSync('token');
    await http.post(`/api/scenic-spots/${scenicId.value}/reviews`, {
      content: newReview.value.content,
      images: JSON.stringify(newReview.value.images.map(img => 
        img.replace(ENV_CONFIG[process.env.NODE_ENV], '')
      ))
    }, {
      headers: { Authorization: `Bearer ${token}` }
    });

    uni.showToast({ title: '评论成功' });
    await fetchDetail();
    newReview.value = { content: '', images: [] };
    closeReviewModal();
  } catch (error) {
    uni.showToast({ title: '提交失败', icon: 'none' });
  }
};
// 构造响应数据
const responseData = {
    ...spot[0],
    reviews: reviews.map(review => {
        try {
            return {
                ...review,
                images: JSON.parse(review.reviewImages)
            };
        } catch (e) {
            console.error('解析评论图片失败:', e.message);
            return {
                ...review,
                images: [] // 解析失败时返回空数组
            };
        }
    })
};
// 弹窗控制
const reviewPopup = ref(null);
const showReviewModal = () => {
  reviewPopup.value.open();
};
const closeReviewModal = () => {
  reviewPopup.value.close();
};

// 收藏切换
const toggleCollect = async () => {
  try {
    const token = uni.getStorageSync('token');
    await http.post(`/api/collections`, {
      scenic_id: scenicId.value,
      action: isCollected.value ? 'remove' : 'add'
    }, {
      headers: { Authorization: `Bearer ${token}` }
    });
    isCollected.value = !isCollected.value;
    uni.showToast({ title: isCollected.value ? '收藏成功' : '已取消收藏' });
  } catch (error) {
    uni.showToast({ title: '操作失败', icon: 'none' });
  }
};

onLoad((options) => {
  scenicId.value = options.id;
  fetchDetail();
});
</script>

